  display: inline-block;
  height:200px;
  align-items: center;
  width:110px;
  padding-right: 80px;
  justify-content: center;
  text-align: center;
}
.indiHolderFinal{
  flex-direction: column;
  display: inline-block;
  height:200px;
  align-items: center;
  width:110px;
  text-align: center;
}
.picProps{
  height:110px;
}


.wordProps{
  font-family: 'OCR A', 'OCR A', monospace;
  font-size: 2.5vh;
  color:#e3c4a8;
  opacity: 0;
  position: relative;
  top:10px;
}

.wordPropss{
  font-family: 'OCR A', 'OCR A', monospace;
  font-size: 2.5vh;
  position: relative;
  top:10px;
  text-align: center;
  color:#e3c4a8;
  opacity: 0;
}
@keyframes abnormal{
  0%{
    height: 65px
  }
  100%{
    height: 0px
  }
}
@keyframes gnormal{
  0%{
    height: 0px
  }
  100%{
    height:65px
  }
}

@keyframes zoomin{
  0%{
    transform: scale(1)

  }
  100%{
    transform: scale(1.3)
  }
}
@keyframes zoomout{
  0%{
    transform: scale(1.3)
  }
  100%{
    transform: scale(1)
  }
}
@keyframes goout{
  0%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}
@keyframes goin{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

.toggleout{
  animation: zoomout 250ms;
  transform: scale(1)
}
.togglein{
  animation: zoomin 250ms;
  transform: scale(1.3)
}

.disappear{
  animation:goout 250ms;
  opacity: 0;
}
.appear{
  animation:goin 250ms;
  opacity: 1;
}

.popin{
  animation:gnormal 250ms;
  height: 65spx;
}

.popout{
  animation:abnormal 250ms;
  height: 0px;
}
