@-webkit-keyframes abnormal{
  0%{
    height: 65px
  }
  100%{
    height: 0px
  }
}
@keyframes abnormal{
  0%{
    height: 65px
  }
  100%{
    height: 0px
  }
}
@-webkit-keyframes gnormal{
  0%{
    height: 0px
  }
  100%{
    height:65px
  }
}
@keyframes gnormal{
  0%{
    height: 0px
  }
  100%{
    height:65px
  }
}

@-webkit-keyframes zoomin{
  0%{
    -webkit-transform: scale(1);
            transform: scale(1)

  }
  100%{
    -webkit-transform: scale(1.3);
            transform: scale(1.3)
  }
}

@keyframes zoomin{
  0%{
    -webkit-transform: scale(1);
            transform: scale(1)

  }
  100%{
    -webkit-transform: scale(1.3);
            transform: scale(1.3)
  }
}
@-webkit-keyframes zoomout{
  0%{
    -webkit-transform: scale(1.3);
            transform: scale(1.3)
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1)
  }
}
@keyframes zoomout{
  0%{
    -webkit-transform: scale(1.3);
            transform: scale(1.3)
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1)
  }
}
@-webkit-keyframes goout{
  0%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}
@keyframes goout{
  0%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}
@-webkit-keyframes goin{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}
@keyframes goin{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

.toggleout{
  -webkit-animation: zoomout 250ms;
          animation: zoomout 250ms;
  -webkit-transform: scale(1);
          transform: scale(1)
}
.togglein{
  -webkit-animation: zoomin 250ms;
          animation: zoomin 250ms;
  -webkit-transform: scale(1.3);
          transform: scale(1.3)
}

.disappear{
  -webkit-animation:goout 250ms;
          animation:goout 250ms;
  opacity: 0;
}
.appear{
  -webkit-animation:goin 250ms;
          animation:goin 250ms;
  opacity: 1;
}

.popin{
  -webkit-animation:gnormal 250ms;
          animation:gnormal 250ms;
  height: 65spx;
}

.popout{
  -webkit-animation:abnormal 250ms;
          animation:abnormal 250ms;
  height: 0px;
}

